import { countriesData, EUData } from "./data"

export const calcEmmissionIn2030 = (emissions, reduction) => {
    return Math.round(emissions*(1-(reduction/100)))
  }

export const calcReduction = (base, current) => {
    return Math.round(1000*(1-(current/base)))/10
  }

export const calcSumOfEmissions = (countries) => {
  let sum = 0
  Object.entries(countries).map(([key, val], i)=>sum+=val[1])
  return sum
}

export const getInitialState = () => {
  let temp = {}
  Object.entries(countriesData).map(([key, val], i)=>temp[key]=[val.goalIn2030, calcEmmissionIn2030(val.GHG1990, val.goalIn2030)])
  return temp
}

export const getInitialSum = () => {
  return [EUData.goalIn2030, calcEmmissionIn2030(EUData.GHG1990, EUData.goalIn2030)]
}

export const kTCO2e = "kT CO2-eq" //"thousands tonnes of CO2 equivalent"