import { initialState, initialSum } from "./initialState";
import { CHANGE_DATA, CALC_SUM } from "./actions";

export const countriesProjectedGHGE = (state = initialState, action) => {
    const temp = { ...state }
    switch (action.type) {
      case CHANGE_DATA:
        temp[ action.id ] = action.data
        return temp
      default:
        return state;
    }
  };

export const projectedSum = (state = initialSum, action) => {
    switch (action.type) {
      case CALC_SUM:
        return action.data;
      default:
        return state;
    }
  };